.content {
  width: 350px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: #e87b00;
}

.todos {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.todo {
  width: 90%;
  padding: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 5px;
  font-weight: bold;
  font-size: .8em;
  text-align: left;
}

.add-form, .add-form form {
  width: 100%;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.update-content-form {
  display: flex;
  justify-content: center;
  align-items: center;
}

input[type="text"] {
  border: none;
  border-radius: 0;
  background-color: #004e61;
  color: #e87b00;
  border: 0.5px solid #e87b00;
  padding: 4.5px;
  text-align: center;
}

button {
  border-radius: 0%;
  background-color: #e87b00;
  border: none;
  font-weight: bold;
  padding: 5px;
  min-width: 70px;
  color: #004e61;
}

button:active {
  background-color: #f00;
}